import React, { useState } from 'react'
import Avatar from './avatar'
import Skills from './skills'
import Contact from './contact'
import Footer from './footer'
import Portfolio from './portfolio'

const Article = () => {

    const [leerMas, setLeerMas] = useState(false)

    const textoCompleto =
        <div className='about-text'>
            <p>
                En la actualidad internet, las redes sociales, las pginas webs y las aplicaciones
                tanto webs como nativas nos han brindado no solo una socializacin a nivel global nunca
                antes vista, sino tambin la oportunidad para generar negocios y emprendimientos
                mucho ms fcil, rpido y eficiente que en dcadas anteriores. Es por dicha razn que
                te ofrezo servicios que te brinden
                a vos la posibilidad de dar un paso ms y darte a conocer al mundo, desde una idea pensada
                desde el punto de vista del usuario (ui/ux) hasta la codificacin, desarrollo y
                produccin de tu pgina web o tu aplicacin en lenguajes de vanguardia en el mercado actual.
            </p>
            <p>
                Figma es -para m- el mejor software para la realizacin de diseos de interfaces de usuario
                aunque dependiendo las necesidades y el contexto, utilizo tambin Adobe Xd.
            </p>
            <p>
                El desarrollo de pginas webs es algo que me apasiona, llevar a cabo un proyecto de un
                e-commerce con herramientas como Wordpress y Woocommerce te brinda la posibilidad de tener
                una web slida y segura.
                Por otro lado si lo que buscas es velocidad para tu Portfolio o emprendimiento, te
                ofrezco pginas desarrolladas en GatsyJS o NextJS, tambin con la posibilidad de contar
                con una plataforma e-commerce slida, eficaz y segura.
            </p>
            <p>
                Est comprobado que en la actualidad es ms la gente que navega por internet con su
                smartphone que con una computadora, esto obliga a cualquier diseador/desarrollador
                a contar con un responsive design, un diseo adaptable a cualquier pantalla. Para
                dicha tarea trabajo con CSS-SASS puro y herramientas como Bootstrap o Materialize.
            </p>
            <p>
                Las aplicaciones se hicieron cada vez ms populares, y la clave est en la facilidad
                que tenemos para utilizarlas desde prcticamente cualquier dispositivo celular,
                tablet o equipo informtico, por eso necesitamos contar con herramientas que nos
                den solidez, velocidad y eficiencia.
                Frameworks como Angular o libreras como React y VUE son las que utilizo
                para desarrollar aplicaciones modernas, funcionales y seguras.
            </p>
            <p>
                Por ltimo y para finalizar esta presentacin, me gustara mencionar un plus que
                tengo a la hora de trabajar, y es el haber pasado por una universidad de Diseo, lo que
                me brind el conocimiento necesario para dominar en su totalidad softwares como
                Illustrator, Photoshop, After Effects, Premiere o Adobe Xd, este plus me da una
                autonoma que no todos poseen a la hora de realizar los proyectos
                y no depender de diseos, animaciones y/o audiovisuales externos.
            </p>
            <p>
                Debajo podrs ver mis <a href="#skills">Skills. </a>
                Pero, tambin me gustara que conocieras mi <a href="#portfolio">portfolio</a>,
                adems pods seguirme en mis <a href="#social">redes sociales, </a> para conocer ms
                sobre mi trabajo o <a href="#contact">contactarme </a> para contar con alguno de mis
                servicios.
            </p>
            <Skills></Skills>
        </div>

    const linkMas = leerMas ? 'Leer menos <<' : 'Continuar leyendo  y ver skills >>'

    return (
        <article className='about-content'>
            <div className='about-text'>

                <h2>About</h2>

                <Avatar></Avatar>

                <p id='p'>
                    Hola! mi nombre es Juan Manuel Crego Risso, tengo 29 aos y soy de Buenos Aires, Argentina.
                    Trabajo como front end developer y me gustara contarte porqu eleg el Front End no solo
                    como profesin, sino tambin como medio por el que expreso mi pasin, el diseo.
                    Desde chico siempre me gust el arte y la tecnologa, por lo que conseguir llevar a
                    cabo mis ideas a travs de una computadora era cuestin de tiempo, de aprendizaje y por
                    sobre todas las cosas, mucha dedicacin, trabajo, horas de estudio, pruebas y errores.
                </p>
                {/* LEER MAS  */}
                {leerMas && textoCompleto}
                <a onClick={
                    () => {
                        setLeerMas(!leerMas)
                    }
                }>
                    <a href='#p' id='link'>
                        {linkMas}
                    </a>
                </a>

                <Portfolio></Portfolio>

                <Contact></Contact>

                <Footer></Footer>
            </div>
        </article>
    )
}

export default Article